<template>
  <div>
    <mt-field label="用户名" placeholder="请输入你的用户名" v-model="username"></mt-field>
    <mt-field label="手机号" placeholder="请输入你的手机号" type="tel" v-model="phone"></mt-field>
    <mt-field label="QQ号" placeholder="请输入你的QQ号" type="number" v-model="qq"></mt-field>

    <mt-radio
      align="right"
      title="单选框列表"
      v-model="value"
      :options="['web前端', 'Java', 'UI设计']">
    </mt-radio>

    <mt-button type="danger" @click="btn">申请试听</mt-button>
  </div>
</template>
<script>
  import { Radio } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  export default {
    data(){
     return {
       username:'',
       phone:'',
       qq:'',
       arry:[]
     }
    },
    methods:{
      btn(){
       if(this.username && this.phone && this.qq){
        MessageBox.alert('课程申请成功').then(action => {
         this.$router.push('/Home')
         });
       }else{
         MessageBox.alert('请填入正确的信息')
       }
      }
    }
  }
</script>




