      title: '公司新闻',
      news: [{ img: __webpack_require__("z21g"), title: 'Vue作者尤雨溪，渐进式解决方案', text: '前端开发工程师是Web前端开发工程师的简称，是近五年才真正开始受到重视的一个新兴职业。Web前端开发技术是一个先易后难的过程，主要包括三个要素：HTML（标准通用标记语言下的一个应用）、级联样式表和JavaScript，这就要求前端开发工程师不仅要掌握基本的Web前端开发技术，网站性能优化、SEO和服务器端的基础知识，而且要学会运用各种工具进行辅助开发以及理论层面的知识，包括代码的可维护性、组件的易用性、分层语义模板和浏览器分级支持等。', time: '2015年2月21日' }, { img: __webpack_require__("KI6U"), title: '如何解决小程序中的各种“没有”', text: 'eto是一个完整和通用的HTML模板,是完美的创意组合——博客和自由职业者作品展示和商业店铺,灵感,产品或服务,这种组合模板覆盖每一个细节。\n' + '主要特色：清洁与轻量级的设计、Masonry瀑布流网格、滚动到顶部辅助元素、提示工具动画、字母特效、作品页面、无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表单、用HTML5和CSS3建造的、700 +字体图标、简单的设置、CSS3动画、包括Font Awesome字体图标库支持响应视频、PHP工作联系表无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PH、跨浏览器的兼容性', time: '2016年2月21日' }, { img: __webpack_require__("PzzX"), title: '深入浅出ES6：魔力四射的生成器 Generators续篇', text: '这是新闻3的详细内容无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表', time: '2017年2月21日' }, { img: __webpack_require__("pZ4c"), title: '一文看透丑陋而又神奇的JSX', text: '这是新闻4的详细内容无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表', time: '2018年2月21日' }, { img: __webpack_require__("/EUA"), title: 'NativeScript 2.2将webpack引入Angular', text: '这是新闻5的详细内容无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表无限的色彩、视差效果、平滑滚动、响应设计、支持响应视频、PHP工作联系表', time: '2019年2月21日' }],
      title2: '行业新动态',
      news2: [{ img: __webpack_require__("aRdM"), title: '饿了么大前端总监：什么样的人可以称为架构师？', text: '企业架构师(Enterprise Architect)是企业IT的总体规划和设计师，他们总揽企业的业务流程、应用、数据、和技术等各个方面，兼分析师、设计师、教师、顾问、监察等数职于一身。好的架构师要具有高屋建瓯的视角，统揽全局的能力，深入的行业业务和技术知识，丰富的开发和实施IT项目的经验。前在中国的多数企业中，企业架构师仍然是一个新的概念。在这本指南中，我们将为您介绍企业架构师面临的最大挑战和应具备的技能，以及在一些具体领域所起到的作用。', time: '2015年2月23日' }, { img: __webpack_require__("XriA"), title: 'PWA实践：理解和创建serverice Worker 脚本', text: '这是行业动态2的详细内容,​随着[1]“互联网+”概念的普及，和移动互联网技术的成熟，越来越多的公司，需要更加智能、高效、合理、人性化的交互，对web前端的需求也会越来越大，同时由于web前端的技术框架种类繁多，更新也非常快，这样也需要大量的前端人员进行自己的技能提升。', time: '2015年2月24日' }, { img: __webpack_require__("2S35"), title: '群分享预告：百度搜索对PWA的探索和初步实践', text: '在我们做的PWA的实例中，基于PWA的Web也拥有了接近原生App一样的体验，因此我们有理由认为PWA会为当前的Web体验带来一次用户体验上的革命同时，因为PWA对于支持上的问用户体验上的提升的落实，百度搜索也会极其的推动PWA在国内Web站点的真正落地，为Web体验的优化做应该做的事情。\n' + '\n' + '例如，我们很快会在自己厂牌App使用的浏览器内核（安卓）中支持ServiceWorker，也会进行持续的探索iOS的实现可能性，为Web站点的开发者提供更多可用的真实案例和可复用的操作方法。\n' + '\n' + '关于开发模式的方面的摸索，我们后续会把我们一些PWA相关的开发经验、工具集等逐步总结发布给大家，我们将会长期推动和跟进PWA。', time: '2015年2月25日' }, { img: __webpack_require__("Ij1x"), title: '滴滴公共EF团队答群问友', text: '这是行业动态4的详细内容,网站重构的目的仅仅是为了让网页更符合Web标准吗？不是！重构的本质是构建一个前端灵活的MVC框架，即HTML作为信息模型（Model），CSS控制样式（View），JavaScript负责调度数据和实现某种展现逻辑（Controller）。同时，代码需要具有很好的复用性和可维护性。这是高效率、高质量开发以及协作开发的基础。', time: '2015年2月26日' }, { img: __webpack_require__("YjAY"), title: '链家网前端总架构师杨永林：我的八年构架师成长之路', text: '前端开发的入门门槛其实非常低，与服务器端语言先慢后快的学习曲线相比，前端开发的学习曲线是先快后慢。所以，对于从事IT工作的人来说，前端开发是个不错的切入点。也正因为如此，前端开发领域有很多自学成“才”的同行，但大多数人都停留在会用的阶段，因为后面的学习曲线越来越陡峭，每前进一步都很难。另一方面，正如前面所说，前端开发是个非常新的职业，对一些规范和最佳实践的研究都处于探索阶段。总有新的灵感和技术不时闪现出来，例如CSS sprite、负边距布局、栅格布局等；各种JavaScript框架层出不穷，为整个前端开发领域注入了巨大的活力；', time: '2015年2月27日' }],
