import Vue from 'vue'
import Router from 'vue-router'

import Job from '../pages/Job'
import Home from '../pages/Home'
import Lesson from '../pages/Lesson'
import Teacher from '../pages/Teacher'
import Subform from '../pages/Subform'
import Environment from '../pages/Environment'
import News from '../pages/News'
import Newst from '../pages/Newst'
import Course from '../pages/Course'
import Hot from '../pages/Hot'
import About from '../pages/About'
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/Job',
      component:Job
    },
    {
      path: '/',
      component:Home
    }, {
      path: '/home',
      component:Home
    }
    , {
      path: '/Lesson',
      component:Lesson
    }
    , {
      path: '/Teacher',
      component:Teacher
    },{
      path: '/Subform',
      component:Subform
    },{
      path: '/Environment',
      component:Environment
    },{
      path: '/About',
      component:About
    },{
      name:"Newse",
      path: '/News',
      component:News
    },{
      name:"Newst",
      path: '/Newst',
      component:Newst
    }
    ,{
      name:"course",
      path: '/Course',
      component:Course
    },{
      path: '/Hot',
      component:Hot
    }
  ]
})



